import React from 'react';
import { Link, TableRow, TableCell } from '@mui/material';
import moment from 'moment';

interface FictionEntryProps {
    link: string;
    name: string;
    lastUpdated?: Date;
    lastWorkedOn?: Date;
    lastWorkedOnDetail?: string;
    children?: any;
    typeOfWork?: string;
}

const WorkEntry: React.FC<FictionEntryProps> = ({ link, name, lastUpdated, lastWorkedOn, lastWorkedOnDetail, typeOfWork }) => {
 
    return (
        <TableRow
              key={link}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
              <Link href={link}>{name}</Link>
              </TableCell>
              <TableCell align="center">{typeOfWork ?? ''}</TableCell>
              <TableCell align="right">{lastUpdated ? moment(lastUpdated).fromNow() : "N/A"}</TableCell>
              <TableCell align="right">{lastWorkedOn ? moment(lastWorkedOn).fromNow() : "N/A"}</TableCell>
              <TableCell align="right">{lastWorkedOnDetail ?? ""}</TableCell>
        </TableRow>
        
    );
}
export default WorkEntry;